import './App.css';
import React , { useState, useEffect } from 'react';
import Videolist from './components/video_list/video_list';

function App() {
  const [videos, setVideos] = useState([]);

  useEffect(()=>{
    const requestOptions = {
      method: 'GET',
      redirect: 'follow'
    };
    
    fetch("https://www.googleapis.com/youtube/v3/videos?part=snippet&chart=mostPopular&maxResults=25&regionCode=KR&key=AIzaSyAquS12T0k8Yk0hCrAmakYErpFaLpMBxko", requestOptions)
      .then(response => response.json())
      .then(result => setVideos(result.items))
      .catch(error => console.log('error', error));
  },[]);

  return (
    <Videolist key={videos.key} videos={videos}/>
  );
}

export default App;
